<template>
  <div class="box">
    <div class='box_title'>拖动下方滑块完成拼图</div>
    <div class="boxImage">
      <div class="imageArea">
        <img class="verifyImage"
             :src="verifyInfo.puzzle" />
      </div>
      <div>
        <img @mousedown="mousedownFn($event)"
             @mousemove="mouseMoveFn($event)"
             @mouseup="moseUpFn($event)"
             :class="{'around animated':btnFailShake}"
             class="verified"
             id="verifiedGap"
             :src="verifyInfo.puzzleGap" />
      </div>
      <div class="suessMask"
           v-show="showStatus">
        <div>{{sucesePrompt}}</div>
      </div>
    </div>
    <div class="drag"
         ref="dragDiv">
      <div class="stroke"></div>
     
      <div class="drag_text">{{confirmWords}}</div>
      <div ref="moveDiv"
           @mousedown="mousedownFn($event)"
           @mousemove="mouseMoveFn($event)"
           @mouseup="moseUpFn($event)"
           :class="{'handler_ok_bg':confirmSuccess,'around animated':btnFailShake}"
           class="handler handler_bg "
           style="position: absolute;top: 0px;left: 0px;"></div>
    </div>
    <!--提示-->
    <div class="result">
      <span class="result_title">{{resultTitle}}</span>
      <img class="reflesh"
           src="../src/assets/image/iconRefresh.png"
           @click="refleshVerify" />
      <img class="out"
           src="../src/assets/image/iconOut.png"
           @click="dropOut" />
    </div>
    <div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import './assets/css/animation.css';
export default {
  name: 'vueVerify',
  //第一次 传入 一个对象 包括拼图 拼图缺口  缺口图片的x坐标
  props: {
    verifyInfo: {
      type: Object,
      required: true
    }

  },
  data () {
    return {
      showStatus: false,  /**/
      sucesePrompt: '验证成功',    /*验证成功的提示*/
      btnFailShake: false,        /*按钮失败抖动 */
      resultTitle: '由心诉科技提供支持', /* 动态结果文字提示 */
      failOpertionNumber: 0,        /* 失败操作次数 失败3次会重新换图*/
      beginClientX: 0,           /*距离屏幕左端距离*/
      mouseMoveStata: false,     /*触发拖动状态  判断*/
      maxwidth: '',               /*拖动最大宽度，依据滑块宽度算出来的*/
      confirmWords: '拖动滑块验证',   /*滑块文字*/
      confirmSuccess: false           /*验证成功判断*/
    }
  },
  components: {

  },
  mounted () {
    window.console.log(this.verifyInfo)
    // 获取滑块的长度
    // this.maxwidth = this.$refs.dragDiv.clientWidth - this.$refs.moveDiv.clientWidth;
    this.maxwidth = 200;
    window.console.log(this.maxwidth);
  },
  methods: {
    //  mousedoen 事件
    mousedownFn: function (e) {
      //获取移动的坐标
      if (!this.confirmSuccess) {
        e.preventDefault && e.preventDefault();   //阻止文字选中等 浏览器默认事件
        this.mouseMoveStata = true;
        this.beginClientX = e.clientX;
      }
    },
    // 验证成功函数 
    successFunction () {
      this.confirmSuccess = true
      this.confirmWords = '验证通过';
      this.showStatus = true;
      if (window.addEventListener) {
        document.getElementsByTagName('html')[0].removeEventListener('mousemove', this.mouseMoveFn);
        document.getElementsByTagName('html')[0].removeEventListener('mouseup', this.moseUpFn);
      } else {
        document.getElementsByTagName('html')[0].removeEventListener('mouseup', () => { });
      }
      document.getElementsByClassName('drag_text')[0].style.color = '#fff'
      document.getElementsByClassName('handler')[0].style.left = this.maxwidth + 'px';
    },
    // mousemove事件           
    mouseMoveFn (e) {
      if (this.mouseMoveStata) {
        let width = e.clientX - this.beginClientX;
        if (width > 0 && width <= this.maxwidth) {
          document.getElementsByClassName('handler')[0].style.left = width + 'px';
          // 设置缺口图片的x坐标的位置
          document.getElementsByClassName('verified')[0].style.left = `${width}px`;
        } else if (width > this.maxwidth) {
          this.successFunction();
        }
      }
    },
    // mouseup事件
    moseUpFn (e) {
      //  1.鼠标放下进行延时验证 
      //  2. 验证失败 进行动画提示 并返回原点更新图片
      // #获取移动的x坐标值 
      this.mouseMoveStata = false;
      var width = e.clientX - this.beginClientX;
      if (width < this.maxwidth) {
        //  失败开启动画
        this.btnFailShake = true;
        //  关闭动画
        setTimeout(() => {
          this.btnFailShake = false;
          this.resultTitle = "拼图失败"
          // 设置滑块的初始的位置
          document.getElementsByClassName('handler')[0].style.left = `${0}px`;
          // 设置缺口图片的x坐标的位置
          document.getElementsByClassName('verified')[0].style.left = `${0}px`;
        }, 500)
      }
    },
    // 刷新数据 拼图
    refleshVerify(){

    },
    // 退出隐藏
    dropOut(){

    }

  },
}
</script>
